/*
* ---------- planet.css ----------
*/
@charset "utf-8";

.planet_wrap{
    position:absolute;
    font-size:5rem;
    color:#fff;
    background-size:contain;
    z-index:9999;
    -webkit-transform:scale(0);
            transform:scale(0);
    transition:scale 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -webkit-animation:0.2s show_planet forwards;
            animation:0.2s show_planet forwards;
}
.planet1{
    top:8%;
    left:20%;
    width: 130px;
    height:130px;
    background:url(/ecosystem/static/media/planet_1.c3c1209d.png) no-repeat;
    background-size:contain;
    -webkit-animation-delay:6.8s;
            animation-delay:6.8s;
}
.planet2{
    top:20%;
    right:16%;
    width: 110px;
    height:110px;
    background:url(/ecosystem/static/media/planet_2.588d6730.png) no-repeat;
    background-size:contain;
    -webkit-animation-delay:7.1s;
            animation-delay:7.1s;
}
.planet3{
    bottom:20%;
    left:20%;
    width: 100px;
    height:100px;
    background:url(/ecosystem/static/media/planet_3.f381970c.png) no-repeat;
    background-size:contain;
    -webkit-animation-delay:7.3s;
            animation-delay:7.3s;
}


/* PLANET POPUP */
.planet_popup_wrap{
    /* display:none;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding-bottom: 4rem;
    border: solid 2px #fff;
    background: #000;
    z-index:99999999999; */
    display:none;
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    -webkit-transform:scale(0);
            transform:scale(0);
    /* -webkit-transition:scale 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
    transition:scale 600ms cubic-bezier(0.175, 0.885, 0.32, 1.275); */
    -webkit-animation:0.2s show_planet forwards;
            animation:0.2s show_planet forwards;
    z-index:9999;
}
.planet_popup1.hide,
.planet_popup2.hide,
.planet_popup3.hide{
    -webkit-animation:0.2s hide_planet forwards;
            animation:0.2s hide_planet forwards;
}
.popup_box{
    position: absolute;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 99999999999;
}
.popup_left{
    float: left;
    position: relative;
}
.popup_left div:nth-of-type(1){
    position: absolute;
    top: 5rem;
    right: 6rem;
    width: 4rem;
    height: 4rem;
    background: #fff;
    border-radius: 5rem;
}
.popup_left div:nth-of-type(2){
    position: absolute;
    top: 12rem;
    right: 2rem;
    width: 6rem;
    height: 6rem;
    background: #eb9035;
    border-radius: 10rem;
}
.popup_center{
    position: relative;
    float: left;
}
.popup_center > div:nth-of-type(1){
    position: absolute;
    bottom: 1rem;
    right: -2rem;
    width: 18rem;
    height: 18rem;
    border-radius: 10rem;
    background: #7b4591;
}
.popup_center > div:nth-of-type(2){
    position: relative;
    width: 30rem;
    height: 30rem;
    border: solid 2px #fff;
    border-radius: 15rem;
}
.popup_center div:nth-of-type(2) div:nth-of-type(1){
    width: 18rem;
    height: 18rem;
    border-radius: 10rem;
    background: #e95f80;
    position: absolute;
    top: -0.5rem;
    left: -0.5rem;
}
.popup_center div:nth-of-type(2) div:nth-of-type(2){
    position: absolute;
    width: 27rem;
    height: 27rem;
    background: #eee;
    border-radius: 14rem;
    top: 1.5rem;
    left: 1.5rem;
}
.popup_center > div:nth-of-type(3){
    position: absolute;
    bottom: -4rem;
    right: 7rem;
    width: 4rem;
    height: 4rem;
    background: #fff;
    border-radius: 10rem;
}
.popup_right{
    position: relative;
    float: left;
}
.popup_right div:nth-of-type(1){
    position: absolute;
    top: 5rem;
    left: -2rem;
    width: 6rem;
    height: 6rem;
    background: #39aaba;
    border-radius: 5rem;
}
.popup_right div:nth-of-type(2){
    position: absolute;
    top: 11rem;
    left: 4rem;
    width: 3rem;
    height: 3rem;
    background: #fff;
    border-radius: 5rem;
}

.planet_popup_wrap.active{
    display:block;
}
.planet_popup_wrap button{
    position: absolute;
    top: -3rem;
    right: -1rem;
    padding: 1rem 1.5rem;
    font-size: 2rem;
    color: #fff;
    background: none;
    border: 0;
    cursor: pointer;
}
.planet_popup_wrap p{
    font-size: 2rem;
    color: #333;
    text-align:center;
    padding: 0.2rem 4rem;
}
.planet_popup_wrap .planet_name{
    font-size: 3rem;
    text-align: center;
    padding:3rem 0 1rem;
    margin-bottom: 1rem;
}
.planet_popup_wrap a{
    font-size: 2rem;
    display: block;
    text-align: center;
    width: 10rem;
    color: #fff;
    padding: 0.6em 0;
    margin: 1.5rem auto 0;
    background:#7b4591;
    border-radius: 3rem;
}



@-webkit-keyframes show_planet{
    80%{
        -webkit-transform:scale(1, 1.1);
                transform:scale(1, 1.1);
    }
    90%{
        -webkit-transform:scale(0.9, 0.9);
                transform:scale(0.9, 0.9);
    }
    to{
        -webkit-transform:scale(1);
                transform:scale(1);
    }
}



@keyframes show_planet{
    80%{
        -webkit-transform:scale(1, 1.1);
                transform:scale(1, 1.1);
    }
    90%{
        -webkit-transform:scale(0.9, 0.9);
                transform:scale(0.9, 0.9);
    }
    to{
        -webkit-transform:scale(1);
                transform:scale(1);
    }
}

@-webkit-keyframes hide_planet{
    from{
        -webkit-transform:scale(1);
                transform:scale(1);
    }
    to{
        -webkit-transform:scale(0);
                transform:scale(0);
    }
}

@keyframes hide_planet{
    from{
        -webkit-transform:scale(1);
                transform:scale(1);
    }
    to{
        -webkit-transform:scale(0);
                transform:scale(0);
    }
}
