/*
* ---------- DataView.css ----------
*/
@charset "utf-8";

.dataContainer{
    position:absolute;
    top:50%;
    right:0;
    width:20rem;
    -webkit-transform:translateY(-50%);
            transform:translateY(-50%);
}
button:focus{
    outline:none;
}

.dataBack{
    position:relative;
    margin:2rem 0;
}
.dataBack ul{
    position:absolute;
    top:1rem;
    right:1rem;
    width:41%;
}
.dataBack ul li{
    margin-bottom:0.3rem;
}
.dataBack ul li p{
    font-size:1rem;
}
.dataBack ul li:last-child p{
    float:left;
    width:50%;
}
.dataBack ul li:last-child div{
    float: right;
    width: 50%;
    height: 9px;
    margin-top: 1px;
    background: #333;
}
.dataBack > div{
    position:relative;
    margin-top:-1rem;
}
.dataBack > div button{
    display:block;
    position:absolute;
    top:0.5rem;
    width:50%;
    font-size:1rem;
    border:none;
    background:transparent;
    cursor:pointer;
}
