    fill:#333;
} */
.btn_reproduce{
    left:0.5rem;
}
.btn_die{
    right:0;
}

.health span,
.poison span{
    display:block;
    width:50%;
    height:100%;
    background:orange;
}
.health.data_status_1 span{
    width:25%;
    background:red;
}
.health.data_status_2 span{
    width:50%;
    background:orange;
}
.health.data_status_3 span{
    width:75%;
    background:yellowgreen;
}
.health.data_status_4 span{
    width:100%;
    background:green;
}
.poison.data_status_1 span{
    width:25%;
    background:green;
}
.poison.data_status_2 span{
    width:50%;
    background:yellowgreen;
}
.poison.data_status_3 span{
    width:75%;
    background:orange;
}
.poison.data_status_4 span{
    width:100%;
    background:red;
}
